import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import {StartpageComponent} from './startpage/startpage.component'
import {BroadcastPageComponent} from './broadcast-page/broadcast-page.component';
import {PlaybackPageComponent} from './playback-page/playback-page.component'

const routes: Routes = [
  {path: "", component: StartpageComponent},
  {path:"broadcaster", component: BroadcastPageComponent},
  {path: "viewer", component:BroadcastPageComponent},
  {path: "playback", component:PlaybackPageComponent}
];
///:session-id

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
