import { Injectable } from '@angular/core';
import { VideocameraService } from './videocamera.service'
import {Observable, Subject} from 'rxjs'

@Injectable({
  providedIn: 'root'
})
export class ChatService {
  messages: Subject<any>;

  constructor(private wsService: VideocameraService) {
    this.messages = <Subject<any>>wsService
      .connect()
      // .next((response:any):any =>{
      //   return response;
      // })
   }

   sendMsg(msg) {
     this.messages.next(msg)
     
   }
   sendVideo(data){
     console.log('sending video',data)
     this.wsService.sendVideo(data)
   }
   streamLive(cb){
     this.wsService.streamLive((data)=>{
       cb(data)
     })
   }
   sendStreamPackets(data){
     this.wsService.sendStreamPackets(data)
   }
   receiveStreamPackets(cb){
     this.wsService.receiveStreamPackets((data)=>{
       cb(data)
     })
   }
   
   fetchVideo(url,cb){
     this.wsService.fetchVideo(url,cb)
   }

   sendVideoForArchive(video){
     this.wsService.sendVideoForArchive(video)
   }

   showPlaybacksAvailable(){
     this.wsService.showPlaybacksAvailable()
   }

   playbackVideoFiles(cb){
     this.wsService.playbackVideoFiles(cb)
   }

}
