import { Injectable } from '@angular/core';
import * as io from 'socket.io-client';
import { Observable } from 'rxjs'
import * as Rx  from 'rxjs'

import { environment } from '../environments/environment'

@Injectable({
  providedIn: 'root'
})
export class VideocameraService {
  private socket;

  constructor() { }
  connect(): Rx.Subject<MessageEvent>{
    this.socket = io(environment.wsUrl)

    let observable = new Observable(observer => {
      this.socket.on('message', (data)=>{
        console.log('received a messge from websocket server');
        observer.next(data)
      })
      return ()=>{
        // this.socket.disconnect()
        this.socket
      }
    })

    let observer = {
      next: (type,data: Object) =>{
        this.socket.emit(type, JSON.stringify(data));
      }
    }

    return Rx.Subject.create(observer,observable)
  }

  sendVideo(data){
    this.socket = io(environment.wsUrl)
    this.socket.emit('video',data)
  }
  streamLive(cb){
    this.socket.on('live',(e)=>{
      console.log('received live stream',e)
      cb(e)
    })
  }
  sendStreamPackets(data){
    this.socket.emit('sendStreamPackets',data)
  }

  receiveStreamPackets(cb){
    this.socket.on('receiveStreamPackets',(e)=>{
      console.log('received stream packets')
      cb(e)
    })
  }

  fetchVideo(url,cb){
    fetch(url,{
      method: 'GET',
      headers: {
        'Access-Control-Allow-Origin': '*'
      },
      mode: 'cors'
  
  }).then((data)=> data.blob()).then((dat)=>{
      console.log(dat)
      cb('',dat)
    }).catch((error)=>{
      console.log(error)
      cb(error,'')
    })
  }

  sendVideoForArchive(video){
    this.socket.emit('sendVideoForArchive',video)
  }

  showPlaybacksAvailable(){
    this.socket.emit('showPlaybacksAvailable',{})
  }

  playbackVideoFiles(cb){
    this.socket.on('playbackVideoFiles',(e)=>{
      cb(e)
    })
  }

}
